import React, { Component } from 'react';
import './itemDetails.css';
import Loading from "../loading";


export default class ItemDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            item: {},
            loading: true
        }
    }

    getItem(id) {
        this.props.getItemFunction(id)
            .then(data => {
                this.setState({
                    item: data,
                    loading: false
                });
            });
    }
    componentDidMount() {
        this.getItem(this.props.itemId);
    }
    componentDidUpdate(prevProps) {
        if (this.props.itemId !== prevProps.itemId) {
            this.getItem(this.props.itemId);
            this.setState({
                loading: true
            });
        }
    }

    renderItem(keys) {
        const data = this.props.transformFunction(this.state.item);
        const result = [];

        for (keys in data) {
            result.push(
                <li key={keys} className="list-group-item d-flex justify-content-between">
                    <span className="term">{keys}</span>
                    <span>{this.state.item[keys]}</span>
                </li>
            );
        }

        return result;
    }

    render() {
        let result;

        if (this.state.loading) {
            result = <Loading />;
        } else {
            result = this.renderItem(this.props.itemKeys);
        }
        console.log("ddd")

        return (
            <div className="char-details rounded">
                <h4>Detailed information</h4>
                <ul className="list-group list-group-flush">
                    {result}
                </ul>
            </div>
        );
    }
}